import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import {Button, ListGroup, ListGroupItem, Grid, Row, Col, Navbar, 
  FormGroup, InputGroup, FormControl, DropdownButton, MenuItem} from 'react-bootstrap/lib';

import ListNotes from './redux/containers/notesContainer';
import AddNotes from './redux/containers/addnotesContainer';

class App extends Component {

  constructor(props){
    super(props);
    this.state = {
      notes: [],
    }
  }

  deleteNote(index) {
    let notesArray = this.state.notes;
    notesArray.splice(index, 1);
    this.setState({notes: notesArray});
  }

  render() {

    return (
      <div className="App" style={{ backgroundColor: "transparent"}}>
        <Navbar style={{ backgroundColor: '#79d279', paddingTop: 12, paddingBottom: 12}}>
          <Navbar.Header>
            <Navbar.Brand>
              <a href="#home" style={{color: "black", fontSize: 22}}>
                Angry Coach 2018
              </a>
            </Navbar.Brand>
          </Navbar.Header>
        </Navbar>

        <Row className="show-grid">
          <Col xs={12} md={2}/>
          <Col xs={12} md={8}>
            <AddNotes />
          </Col>
          <Col xs={12} md={2}/>
        </Row>

        <ListNotes />

      </div>
    );
  }
}

export default App;


